
import vue from 'vue'
import VueRouter from "vue-router";
vue.use(VueRouter);
/* import Error from '../views/pages/Error.vue' */
import Home from '../views/web/Home.vue';
import Depositar from '../views/web/Depositar.vue';
import Sacar from '../views/web/Sacar.vue';
import Extrato from '../views/web/Extrato.vue';
import Profile from '../views/web/Profile.vue';

const routers = [
  {
    path: '/',
    name: 'Inicio',
    component: Home
  },
  {
    path: '/depositar',
    name: 'Depositar',
    component: Depositar
  },
  {
    path: '/sacar',
    name: 'Sacar',
    component: Sacar
  },
  {
    path: '/extrato',
    name: 'Extrato',
    component: Extrato
  },
  {
    path: '/profile',
    name: 'Profile',
    component: Profile
  }
]

const router = new VueRouter({
  mode: 'history',
  routes: routers

})

export default router;