<template>
  <div class="side-bar">
    <div class="logo">
      <h1 style="cursor:pointer;">
        <img src="../../assets/logo.svg" alt="" />
      </h1>
    </div>
    <hr>
    <div class="nav">
      <div id="0" class="item item-selected" @click="toPathUrl('0', '/')">
        <img src="../../assets/Icon - In√≠cio.svg" alt="">
        <div class="text">In√≠cio</div>
      </div>
      <div id="1" class="item" @click="toPathUrl('1', '/depositar')">
        <img src="../../assets/Icon - Depositar-1.svg" alt="">
        <div class="text">Depositar</div>
      </div>
      <div id="2" class="item" @click="toPathUrl('2', '/sacar')">
        <img src="../../assets/Icon - Depositar.svg" alt="">
        <div class="text">Sacar</div>
      </div>
      <div id="3" class="item" @click="toPathUrl('3', '/extrato')">
        <img src="../../assets/Icon - Extrato.svg" alt="">
        <div class="text">Extrato</div>
      </div>
      <div id="4" class="item" @click="toPathUrl('4', '/profile')">
        <img src="../../assets/Icon - Minha conta.svg" alt="">
        <div class="text">Minha Conta</div>
      </div>

    </div>
    <!-- <div class="help">
      <small>Preciso de ajuda</small>
    </div> -->
  </div>
</template>

<script>

export default {
  name: 'sideBar',
  data() {
    return {
      tabSection: 'dados',
    }
  },
  methods: {
    toPathUrl(rotaStyle, rota) {
      this.$router.push(rota)
      this.addClass(rotaStyle)
    },
    addClass(el) {
      this.removeClass();
      document.getElementById(`${el}`).classList.add('item-selected')
    },
    removeClass() {
      let lis = document.querySelectorAll('.item');
      lis.forEach((i) => {
        i.classList.remove('item-selected');
      })
    },

  }

}
</script>

<style scoped>
.side-bar {
  background-color: rgb(22, 22, 22) !important;
  color: white;
  min-width: 300px !important;
  min-height: 100%;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}



.logo {
  margin-top: 10px;
}

.nav {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: start;
}

.item {
  min-width: 208px;
  height: 40px;
  display: flex;
  /*  justify-content: center; */
  padding-left: 15px;
  padding-right: 15px;
  align-items: center;
  margin-top: 15px;
  border-radius: 5px;
  cursor: pointer;
}

.item-selected {
  background-color: rgba(255, 255, 255, 0.219);
  border-left: 4px solid #EFEF18;
}

.text {
  color: #9B9B9B;
  font-weight: bold;
  margin-left: 15px;
}

hr {

